.popup__input{
    width: 100%;
    padding-bottom: 10px;
    outline: none;
    border: 0;
    border-bottom: 1px solid rgba(0,0,0,.2);
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    color: #000000;
    box-sizing: border-box;
}

@media screen and (max-width: 545px) {
    .popup__input {
        margin: 0;
        padding-bottom: 8px;
    }
}